import React, {Component, Fragment} from "react";
import Helmet from "react-helmet";
import {Link} from "react-router-dom";
import axios from "axios";
import Layout from "../../components/Layout";
import Document from "../../components/Document";
import routes from "../../routes";

import "./Newsletter.css";

class Newsletter extends Component {
    constructor(props) {
        super(props);
        this.state = {
            newsletter: null,
            error: false,
        };
    }
    componentDidMount() {
        this.loadNewsletter();
    }
    loadNewsletter() {
        axios({
            method: "get",
            url: `https://www.orpose.se/api/newsletters/${this.props.match.params.id}`
        })
        .then(response => {
            this.setState({newsletter: response.data.newsletter});
        })
        .catch(err => {
            this.setState({error: true});
        });
    }
    render() {
        if (this.state.newsletter) {
            let {title, author, paragraphs} = this.state.newsletter;
            return (
                <Layout>
                    <Helmet>
                        <title>Nyheter | Orpose.se</title>
                        <meta name="description" content={"Hitta rtt produkt fr just dina krav och preferenser p Orpose"} />
                        <link rel="canonical" href={`https://www.orpose.se/newsletters/${this.props.match.params.id}`} />
                    </Helmet>
                    <Document>
                        <h3>{title}</h3>
                        <p className="newsletter-author">Av {author}</p>
                        {paragraphs.map(({id, text}) => (
                            <Fragment>
                                <br />
                                <p key={id}>{text}</p>
                            </Fragment>
                        ))}
                    </Document>
                </Layout>
            );
        } else if (this.state.error) {
            return (
                <Layout>
                    <Helmet>
                        <title>Nyheten finns inte | Orpose.se</title>
                        <meta name="description" content={"Hitta rtt produkt fr just dina krav och preferenser p Orpose"} />
                        <link rel="canonical" href={`https://www.orpose.se/newsletters/${this.props.match.params.id}`} />
                    </Helmet>
                    <Document>
                        <h2>Nyheten finns inte</h2>
                        <p>Nyheten du frskte hitta finns inte.</p>
                        <p><Link to={routes.newsletters}>Tillbaka till nyheterna</Link></p>
                    </Document>
                </Layout>
            );
        } else {
            return (
                <Layout>
                    <Helmet>
                        <title>Nyheter | Orpose.se</title>
                        <meta name="description" content={"Hitta rtt produkt fr just dina krav och preferenser p Orpose"} />
                        <link rel="canonical" href={`https://www.orpose.se/newsletters/${this.props.match.params.id}`} />
                    </Helmet>
                    <Document>
                    </Document>
                </Layout>
            );
        }
    }
}

export default Newsletter;
