import React from "react";
import Helmet from "react-helmet";
import Layout from "../../components/Layout";
import Customization from "./Customization";

/**
 * The laptop category customizer.
 */
const LaptopCategory = props => {
    // The customizers for the category.
    let customizers = [
        {type: "usage", title: "Vlj anvndningsomrde", name: "usage", expanded: true, options: [
            {name: "general", title: "Allmnt", description: "Surfande och Office-program"},
            {name: "gaming", title: "Gaming", description: "Spel och andra resurskrvande program"},
        ]},
        {type: "range", title: "Vlj budget (kr)", name: "price", expanded: true, ranges: {
            general: {min: 0, max: 24000, step: 1000},
            gaming: {min: 4000, max: 35000, step: 1000},
        }, description: "Priset p laptopen"},
        {type: "priority", title: "Vlj dina prioriteringar", name: "priorities", expanded: true, options: [
            {name: "performance", title: "Prestanda", description: "Hg prestanda"},
            {name: "storage", title: "Lagring", description: "Lagra stora filer"},
            {name: "screen", title: "Skrm", description: "Kvaliteten p skrmen"},
            {name: "weight", title: "Vikt", description: "Ltt att bra"},
            {name: "battery", title: "Batteritid", description: "Lng batteritid"},
        ]},
        {type: "range", title: "Vlj skrmstorlek (tum)", name: "size", expanded: false, ranges: {
            general: {min: 10, max: 17, step: 1},
            gaming: {min: 10, max: 17, step: 1},
        }, description: "Storleken p skrmen"},
    ];
    
    let steps = ["usage"];
    if (window.google_optimize.get("Kh3bfh9oQWyAOB_XEw71yQ") === "1")
        steps = ["usage", "price", "priorities"];
    
    return (
        <Layout>
            <Helmet>
                <title>Hitta rtt laptop fr just dig | Orpose.se</title>
                <meta name="description" content="Verktyget som hjlper dig att hitta rtt laptop fr just dina krav och preferenser. Hitta ven produkter ssom laptops, ..." />
                <link rel="canonical" href="https://www.orpose.se/category/laptop" />
            </Helmet>
            <Customization customizers={customizers} steps={steps} />
        </Layout>
    );
};

export default LaptopCategory;
