import React, {createRef} from "react";
import UsageCustomizer from "../../components/customizers/Usage";
import RangeCustomizer from "../../components/customizers/Range";
import PrioritiesCustomizer from "../../components/customizers/Priorities";
import CheckboxCustomizer from "../../components/customizers/Checkbox";

import "./Customizers.css";

/**
 * The customizer column.
 */
const Customizers = (props) => {
    let toggleRef = createRef();
    let toggleEnabled = props.step >= props.steps.length;
    
    let open = event => {
        if (!toggleEnabled) return;
        props.setOpen(true);
    };
    let apply = event => {
        if (!toggleEnabled) return;
        
        if (window.innerWidth <= 600)
            toggleRef.current.scrollIntoView({block: "nearest"});
        props.setOpen(false);
    };
    let toggle = event => {
        if (props.open) {
            apply();
        } else{
            open();
        }
    };
    
    return (
        <div ref={toggleRef} className={props.open ? "customizers" : "customizers closed"}>
            <button className={toggleEnabled ? "toggle-button" : "toggle-button disabled"} onClick={toggle}>
                Alternativ
                <svg viewBox="0 0 20 20" width="20px" height="20px">
                    <path fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round" d="M 2 14 L 10 6 L 18 14" />
                </svg>
            </button>
            {props.customizers.map(({type, title, name, description, ...data}, i) =>{
                let unlockedStep = props.steps.indexOf(name);
                if (unlockedStep === -1) unlockedStep = props.steps.length;
                
                let value = props.selection[name];
                let disabled = unlockedStep > props.step;
                return (
                    <Collapsable key={name}
                        title={title}
                        description={description}
                        disabled={disabled}
                        closeDisabled={props.step < props.steps.length}
                        expanded={props.expanded[i]}
                        toggleCollapse={() => props.toggleCollapse(i)}
                    >
                        { type === "usage" ? (
                            <UsageCustomizer
                                options={data.options}
                                selected={value}
                                onChange={value => props.onValueChange(name, value)}
                            />
                        ) : type === "range" ? (
                            <RangeCustomizer
                                range={data.ranges[props.selection.usage]}
                                selectedMin={value.min} selectedMax={value.max}
                                onChange={(min, max) => props.onValueChange(name, {min, max})}
                                disabled={disabled}
                            />
                        ) : type === "priority" ? (
                            <PrioritiesCustomizer
                                options={data.options}
                                value={value}
                                onOptionChange={(opt_name, value) => props.onOptionChange(name, opt_name, value)}
                                disabled={disabled}
                            />
                        ) : type === "checkbox" ? (
                            data.options.map(option => (
                                <CheckboxCustomizer
                                    key={option.name} title={option.title}
                                    checked={value[option.name]}
                                    onChange={value => props.onOptionChange(name, option.name, value)}
                                    disabled={disabled}
                                />
                            ))
                        ) : null}
                    </Collapsable>
                );
            })}
            <button className={toggleEnabled ? "close-button" : "close-button disabled"} onClick={apply}>Verkstll</button>
        </div>
    );
};

const Collapsable = (props) => {
    let toggle = event => {
        if (!props.closeDisabled)
            props.toggleCollapse();
    };
    
    let buttonClasses = props.disabled ? "disabled" : "";
    if (props.closeDisabled) buttonClasses += " close-disabled";
    
    return (
        <div className={props.expanded ? null : "collapsed"} title={props.description}>
            <button className={buttonClasses} onClick={toggle}>
                <span>{props.title}</span>
                <svg viewBox="0 0 20 20" width="20px" height="20px">
                    <path fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round" d="M 2 14 L 10 6 L 18 14" />
                </svg>
            </button>
            {props.expanded && (
                <div>
                    {props.children}
                </div>
            )}
        </div>
    );
}

export default Customizers;
