import React, {Component, createRef} from "react";
import Title from "./Title";

import "./Info.css";

function formatInt(value) {
    return value.toString().replace(/.(?=(...)+$)/g, "$& ");
}

/**
 * The information header.
 */
const Info = props => {
    let {product, activeMeta} = props;
    
    let classes = "info";
    if (props.mobile) classes += " mobile";
    
    return (
        <div className={classes}>
            {!props.mobile && <Title name={product.name}/>}
            <p className="price">{formatInt(activeMeta.campaign_price || activeMeta.standard_price)} kr</p>
            <Dropdown
                selected={activeMeta.host.name}
                metaProducts={product.meta_products}
                onSelect={props.onHostChange}
            />
            <a className="go-to" href={activeMeta.url}>G till {activeMeta.host.short_url}</a>
        </div>
    );
};

class Dropdown extends Component {
    constructor(props) {
        super(props);
        this.state = {open: false};
        this.onOpen = this.onOpen.bind(this);
        this.onDocumentPress = this.onDocumentPress.bind(this);
        this.ref = createRef();
    }
    
    componentDidMount() {
        document.body.addEventListener("mousedown", this.onDocumentPress, true);
        document.body.addEventListener("touchstart", this.onDocumentPress, true);
    }
    componentWillUnmount() {
        document.body.removeEventListener("mousedown", this.onDocumentPress, true);
        document.body.removeEventListener("touchstart", this.onDocumentPress, true);
    }
    
    /**
     * Triggered when the main button is pressed.
     */
    onOpen(event) {
        event.stopPropagation();
        
        // Toggle the open state.
        this.setState(state => ({open: !state.open}));
    }
    /**
     * Triggered when the mouse is pressed anywhere in the document.
     */
    onDocumentPress(event) {
        let inside = this.ref.current.contains(event.target);
        this.setState(state => ({open: state.open && inside}));
    }
    /**
     * Triggered when a value is selected.
     */
    onSelect(i, event) {
        event.stopPropagation();
        
        // Closes the dropdown and triggers the handler.
        this.setState({open: false});
        this.props.onSelect(i);
    }
    render() {
        return (
            <div ref={this.ref} className="dropdown">
                <button onClick={this.onOpen}>
                    Hos {this.props.selected}
                    <svg viewBox="0 0 12 7" width="12px">
                        <path d="M 1 1 L 6 6 L 11 1" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round" />
                    </svg>
                </button>
                {this.state.open && (
                    <div>
                        {this.props.metaProducts.map(({host, campaign_price, standard_price}, i) => (
                            <button key={i} onClick={e => this.onSelect(i, e)}>
                                <span className="website-name">{host.name}</span>
                                <span className="website-price">{formatInt(campaign_price || standard_price)} kr</span>
                            </button>
                        ))}
                    </div>
                )}
            </div>
        );
    }
}

export default Info;
