import {Helmet} from "react-helmet";
import React, {Component, Fragment} from "react";
import {Link} from "react-router-dom";
import routes from "../../routes";
import Layout from "../../components/Layout";
import FullHeight from "../../components/FullHeight";
import Info from "../../components/product/Info";
// import PageSelector from "../../components/product/PageSelector";
import Preview from "../../components/product/Preview";
import Specifications from "../../components/product/Specifications";
import Title from "../../components/product/Title";
import Document from "../../components/Document";
import axios from "axios";

import "./Product.css";

class Product extends Component {
    constructor(props) {
        super(props)

        this.state = {
            product: null,
            notFound: false,
            activeWebsite: null,
            page: 0,
            mobile: window.innerWidth <= 600,
        };
        
        this.changePage = this.changePage.bind(this);
        this.changeHost = this.changeHost.bind(this);
        this.onResize = this.onResize.bind(this);
    }

    componentDidMount() {
        window.addEventListener("resize", this.onResize);
        
        axios({
            method: "get",
            url: `https://www.orpose.se/api/${this.props.type}/${this.props.id}/${this.props.slug}`
        })
        .then(response => {
            let product = response.data;
            this.setState({product, activeMeta: product.meta_products[0]});
        })
        .catch(error =>{
            this.setState({notFound: true});
        });
    }
    componentWillUnmount() {
        window.removeEventListener("resize", this.onResize);
    }
    
    /**
     * Change to the mobile layout if the width is small enough
     */
    onResize() {
        this.setState({mobile: window.innerWidth <= 600});
    }
    
    /**
     * Changes the currently selected website.
     */
    changeHost(index) {
        this.setState(state => ({activeMeta: state.product.meta_products[index]}));
    }
    
    /**
     * Changes the currently displayed page.
     */
    changePage(page) {
        this.setState({page});
    }

    render() {
        // The canonical url to the page.
        let canonical = `https://www.orpose.se/${this.props.type}/${this.props.id}/${this.props.slug}`;
        
        if (this.state.product) {
            let {product, activeMeta} = this.state;
            return (
                <Layout>
                    <Helmet>
                        <title>{product.name}, Hitta rtt teknikprodukt fr just dig | Orpose.se</title>
                        <meta name="description" content={product.name + ", hitta rtt produkt fr just dina krav och preferenser p Orpose"} />
                        <link rel="canonical" href={canonical} />
                    </Helmet>
                    <div className="product">
                        <div className={this.state.mobile ? "content mobile" : "content"}>
                            {this.state.mobile ? (
                                <Fragment>
                                    <FullHeight className="full-height">
                                        <Title name={product.name} />
                                        <Preview images={product.images} name={product.name} mobile={this.state.mobile} />
                                        <Info
                                            product={product}
                                            activeMeta={activeMeta}
                                            mobile={true}
                                            onHostChange={this.changeHost}
                                        />
                                    </FullHeight>
                                    { /* <Specifications specifications={product.specifications} /> */ }
                                </Fragment>
                            ) : (
                                <Fragment>
                                    <Preview images={product.images} name={product.name} mobile={this.state.mobile} />
                                
                                    {this.state.page === 0 ? (
                                        <Info
                                            product={product}
                                            activeMeta={activeMeta}
                                            mobile={false}
                                            onHostChange={this.changeHost}
                                        />
                                    ) : this.state.page === 1 ? (
                                        <Specifications specifications={product.specifications} />
                                    ) : null}
                                    
                                    { /*<PageSelector
                                        pages={["Information", "Specifikationer"]}
                                        selected={this.state.page}
                                        onSelect={this.changePage}
                                    /> */ }
                                </Fragment>
                            )}
                            
                        </div>
                        <div className="advertisements" />
                    </div>
                </Layout>
            );
        } else if (this.state.notFound) {
            // Error page if the product couldn't load.
            return (
                <Layout>
                    <Helmet>
                        <title>Produkten finns inte | Orpose.se</title>
                    </Helmet>
                    <Document>
                        <h2>Produkten finns inte</h2>
                        <p>Produkten du frskte hitta finns inte.</p>
                        <p><Link to={routes.home}>Tillbaka till startsidan</Link></p>
                    </Document>
                </Layout>
            );
        } else {
            // Empty page until the product loads.
            return (
                <Layout>
                    <Helmet>
                        <title>Hitta rtt teknikprodukt fr just dig | Orpose.se</title>
                        <meta name="description" content="Hitta rtt produkt fr just dina krav och preferenser p Orpose" />
                        <link rel="canonical" href={canonical} />
                    </Helmet>
                    <div className="product" />
                </Layout>
            );
        }
    }
}

export default Product;
