import React from "react";
import Helmet from "react-helmet";
import Layout from "../../components/Layout";
import routes from "../../routes";
import {Link} from "react-router-dom";

import "./Sitemap.css";

const Sitemap = () => {
    return (
        <Layout>
            <Helmet>
                <title>Sitemap | Orpose.se</title>
                <meta name="description" content={"Hitta rtt produkt fr just dina krav och preferenser p Orpose"} />
                <link rel="canonical" href={"https://www.orpose.se/sitemap"} />
            </Helmet>
            <div className="sitemap">
                <h2>Sitemap</h2>
                <h3>Categories</h3>
                <Link to={routes.laptopCategory}>Laptop</Link>
            </div>
        </Layout>
    )
}

export default Sitemap;
