import React, {Component} from 'react';

import "./Feedback.css";

/**
 * A text area used to submit feedback.
 */
class Feedback extends Component {
    constructor() {
        super();
        this.areaRef = React.createRef();
        this.state = {
            sent: false,
        };
        this.sentTimeout = null;
    }
    onChange() {
        // Limit the text to 128 characters.
        let textArea = this.areaRef.current;
        textArea.value = textArea.value.substring(0, 128);
    }
    submit() {
        // Limit the text to 128 characters.
        let textArea = this.areaRef.current;
        let text = textArea.value.substring(0, 128);
        
        if (text.length > 0) {
            // Submit the feedback.
            fetch('https://www.orpose.se/api/feedback', {
                method: 'POST',
                headers: {
                    "Content-Type": "application/x-www-form-urlencoded",
                },
                body: "message=" + encodeURIComponent(text),
            }).then(response => {
                // Clear the textarea value.
                textArea.value = "";
            
                // Show the sent message.
                this.setState({sent: true});
                
                // Set a timeout to hide the message.
                if (this.sentTimeout === null) clearTimeout(this.sentTimeout);
                this.sentTimeout = setTimeout(() => {
                    this.setState({sent: false});
                    this.sentTimeout = null;
                }, 10000);
            });
        }
    }
    render() {
        return (
            <div className="feedback-area">
                <textarea ref={this.areaRef} placeholder="Skriv feedback hr" onChange={this.onChange.bind(this)}></textarea>
                <button onClick={this.submit.bind(this)}>Skicka</button>
                <p className={this.state.sent ? "visible" : null}>Feedback skickad!</p>
            </div>
        );
    }
}

export default Feedback;
