import React from "react";
import {Link} from "react-router-dom";
import {Helmet} from "react-helmet";
import routes from "../routes.js"
import Layout from "../components/Layout";
import FullHeight from "../components/FullHeight";
import background from "../assets/main-page-background.png";

import "./Orpose.css";

function Category(props) {
    return (
        <Link className="category" to={props.url}>
            <img src={require("../assets/"+props.image)} className="category-image" alt="kategori ikon" />
            <h3 className="category-name">{props.name}</h3>
        </Link>
    );
}

