import React, {Fragment, Component, createRef} from "react";
import Checkbox from "./Checkbox";

import "./Priorities.css";

/**
 * A group of priority selectors.
 */
const Priorities = props => {
    
    // The amount of selected priorities.
    let amountSelected = props.options.reduce((acc, option) => acc + (props.value[option.name] !== 0), 0);
    let disableOthers = amountSelected >= 3 || props.value.none;
    
    return (
        <Fragment>
            {props.options.map(option => (
                <Priority
                    key={option.name} title={option.title}
                    priority={props.value[option.name]}
                    onChange={value => props.onOptionChange(option.name, value)}
                    disabled={props.disabled || (props.value[option.name] === 0 && disableOthers)}
                    description={option.description}
                />
            ))}
            <Checkbox
                title="Inget av ovanstende"
                checked={props.value.none}
                onChange={value => props.onOptionChange("none", value)}
                disabled={props.disabled || amountSelected > 0}
            />
        </Fragment>
    );
};

/**
 * A priority customizer. Similar to the checkbox customizer but shows a
 * dropdown when selected.
 */
const Priority = (props) => {
    // Trigger the onChange listener when clicked.
    let onClick = event => {
        if (props.disabled) return;
        
        event.preventDefault();
        props.onChange(props.priority ? 0 : 3);
    };
    
    let classes = "priority-container";
    if (props.disabled) classes += " disabled";
    
    return (
        <div className={classes} onClick={onClick} title={props.disabled ? null : props.description}>
            <div className="checkbox">
                {props.priority > 0 && (
                    <svg viewBox="0 0 24 24">
                        <path
                            fill="none" stroke="black" strokeWidth="2px"
                            strokeLinecap="round" strokeLinejoin="round"
                            d="M 4 12 L 10 18 L 20 5"
                        />
                    </svg>
                )}
            </div>
            <p>{props.title}</p>
            {props.priority > 0 && (
                <Dropdown
                    values={["Knappt viktigt", "Lite viktigt", "Ganska viktigt", "Mycket viktigt"]}
                    selected={props.priority - 1}
                    onSelect={i => props.onChange(i + 1)}
                />
            )}
        </div>
    );
};

class Dropdown extends Component {
    constructor(props) {
        super(props);
        this.state = {open: false};
        this.onOpen = this.onOpen.bind(this);
        this.onDocumentPress = this.onDocumentPress.bind(this);
        this.ref = createRef();
    }
    
    componentDidMount() {
        document.body.addEventListener("mousedown", this.onDocumentPress, true);
        document.body.addEventListener("touchstart", this.onDocumentPress, true);
    }
    componentWillUnmount() {
        document.body.removeEventListener("mousedown", this.onDocumentPress, true);
        document.body.removeEventListener("touchstart", this.onDocumentPress, true);
    }
    
    /**
     * Triggered when the main button is pressed.
     */
    onOpen(event) {
        event.stopPropagation();
        
        // Toggle the open state.
        this.setState(state => ({open: !state.open}));
    }
    /**
     * Triggered when the mouse is pressed anywhere in the document.
     */
    onDocumentPress(event) {
        let inside = this.ref.current.contains(event.target);
        this.setState(state => ({open: state.open && inside}));
    }
    /**
     * Triggered when a value is selected.
     */
    onSelect(i, event) {
        event.stopPropagation();
        
        // Closes the dropdown and triggers the handler.
        this.setState({open: false});
        this.props.onSelect(i);
    }
    render() {
        return (
            <div ref={this.ref} className="dropdown" title="Hur viktigt det r">
                <button onClick={this.onOpen}>
                    {this.props.values[this.props.selected]}
                    <svg viewBox="0 0 12 7" width="12px">
                        <path d="M 1 1 L 6 6 L 11 1" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round" />
                    </svg>
                </button>
                {this.state.open && (
                    <div>
                        {this.props.values.map((text, i) => (
                            <button key={i} onClick={e => this.onSelect(i, e)}>{text}</button>
                        ))}
                    </div>
                )}
            </div>
        );
    }
}

export default Priorities;
