import React from "react";
import Helmet from "react-helmet";
import Layout from "../../components/Layout";
import Document from "../../components/Document";

const Contact = () => {
    return (
        <Layout>
            <Helmet>
                <title>Kontakta oss | Orpose.se</title>
                <meta name="description" content={"Hitta rtt produkt fr just dina krav och preferenser p Orpose"} />
                <link rel="canonical" href={"https://www.orpose.se/contact"} />
            </Helmet>
            <Document>
                <h3>Kontakta oss</h3>
                <p>Har du frgor om Orpose?</p>
                <p>Kontakta oss via mail: <a href="mailto:contact@orpose.com">contact@orpose.com</a>.</p>
                
                <h4>Fretagsinformation</h4>
                <p>Orpose Group AB</p>
                <p>Organisationsnummer: 5592338098</p>
                <p>Sotargatan 1, 553 23 Jnkping</p>
                <p>VD: Emil Wagman</p>
            </Document>
        </Layout>
    );
};

export default Contact;
