import React from "react";
import {CarouselProvider, Slider, Slide, Dot, DotGroup} from "pure-react-carousel";

import "pure-react-carousel/dist/react-carousel.es.css";
import "./Preview.css";

/**
 * The product preview slideshow.
 */
const Preview = props => {
    return (
        <div className="preview">
            <CarouselProvider
                naturalSlideWidth={150}
                naturalSlideHeight={100}
                totalSlides={props.images.length}
            >
                <Slider>
                    {props.images.map((image, i) => (
                        <Slide key={image.url} index={i}>
                            <div className="preview-image" style={{backgroundImage: `url(${image.url})`}} />
                            <p className="preview-image-source">Bild frn {image.host.name}</p>
                        </Slide>
                    ))}
                </Slider>
                {props.mobile ? (
                    <DotGroup className="dots" />
                ) : (
                    <div className="thumbnails">
                        {props.images.map((image, i) => (
                            <Dot key={image} slide={i} className="thumbnail">
                                <img src={image.url} alt={"produktbild fr " + props.name} />
                            </Dot>
                        ))}
                    </div>
                )}
            </CarouselProvider>
            
        </div>
    );
};

export default Preview;
