import React, {Component} from "react";
import {Link} from "react-router-dom";
import CookieConsent from "react-cookie-consent";
import routes from "../routes.js";
import Footer from "./nav/Footer.js";
import Menu from "./nav/Menu.js";
import Header from "./nav/Header.js";

/**
 * The layout of a page. Adds a header, footer and sidebar to the content.
 */
class Layout extends Component {
    constructor() {
        super();
        this.state = { sidebar: false };
        this.toggleMenu = this.toggleMenu.bind(this);
    }
    toggleMenu(event) {
        event.preventDefault();
        this.setState(state => ({menu: !state.menu}));
    }
    render() {
        if (document.location.hostname.search("orpose") !== -1) {
            window.gtag("config", "UA-154547946-1", {'page_path': window.location.pathname + window.location.search});
        }

        return (
            <div className="layout-body">
                <CookieConsent 
                    buttonText="Jag frstr" 
                    cookieName="cookieConsent" 
                    buttonStyle={{ background: "white", fontSize: "13px" }}
                    style={{ position: "fixed", bottom: "0", left: "0", width: "100%" }} 
                    expires={150} 
                >
                Fr att kunna frbttra Orpose anvnder vi oss av kakor (cookies), genom fortsatt anvndning av webbsidan accepterar du detta. <Link to={routes.cookies} style={{color: "white"}}>Mer information</Link>
                </CookieConsent>
                <Header toggleMenu={this.toggleMenu} />
                <Menu toggleMenu={this.toggleMenu} visible={this.state.menu} />
                {this.props.children}
                <Footer />
            </div>
        );
    }
}

export default Layout;
