import React, {Component} from "react";
import Helmet from "react-helmet";
import {Link} from "react-router-dom";
import axios from "axios";
import Layout from "../../components/Layout";
import Document from "../../components/Document";
import routes from "../../routes";

import "./Newsletter.css";

class Newsletters extends Component {
    constructor(props) {
        super(props);
        this.state = {
            newsletters: null,
        };
    }
    componentDidMount() {
        this.loadNewsletters();
    }
    loadNewsletters() {
        axios({
            method: "get",
            url: `https://www.orpose.se/api/newsletters`
        })
        .then(response => {
            let newsletters = response.data.newsletters;
            newsletters.reverse();
            this.setState({newsletters});
        })
        .catch(err => console.error("Could not load the newsletters: " + err));
    }
    render() {
        let {newsletters} = this.state;
        return (
            <Layout>
                <Helmet>
                    <title>Nyheter | Orpose.se</title>
                    <meta name="description" content={"Hitta rtt produkt fr just dina krav och preferenser p Orpose"} />
                    <link rel="canonical" href={"https://www.orpose.se/newsletters"} />
                </Helmet>
                <Document>
                    <h3>Nyheter</h3>
                    {newsletters && newsletters.map(({title, creation_date, paragraphs, id}) =>(
                        <Link to={routes.newsletters + "/" + id} key={id} className="newsletter">
                            <h4>{title}</h4>
                            <p className="newsletter-author">{creation_date}</p>
                            <p>{paragraphs[0].text}</p>
                        </Link>
                    ))}
                </Document>
            </Layout>
        );
    }
};

export default Newsletters;
