import React, {Component} from "react";

class FullHeight extends Component {
    constructor() {
        super();
        this.state = {
            width: window.innerWidth,
            height: window.innerHeight
        };

        // Test if it is an iOS device.
        this.isIOS = (/iPad|iPhone|iPod/.test(navigator.userAgent) || (navigator.platform === 'MacIntel' && navigator.maxTouchPoints > 1)) && !window.MSStream;

        this.onResize = this.onResize.bind(this);
    }
    onResize() {
        this.setState(state => {
            let width = window.innerWidth;
            let height = window.innerHeight;

            // iOS address bar collapse/expand.
            if (state.width === window.innerWidth && this.isIOS)
                height = Math.min(height, state.height);

            return {width, height};
        });
    }
    componentDidMount() {
        window.addEventListener('resize', this.onResize);
    }
    componentWillUnmount() {
        window.removeEventListener('resize', this.onResize);
    }
    render() {
        return (
            <div style={{
                minHeight: `calc(${this.state.height}px - var(--header-height))`,
                transition: this.isIOS ? "min-height .5s" : null,
            }} {...this.props}>
                {this.props.children}
            </div>
        );
    }
}

export default FullHeight;
