import React from "react";
import UsageIcon, {fgColor, bgImage} from "../../assets/icons/Usage";

import "./Usage.css";

/**
 * A usage selector. Allows the user to select the main usage of the product.
 */
const Usage = props => {
    return (
        <div className="usages">
            {props.options.map(({name, title, description}) => (
                <UsageButton key={name}
                    selected={props.selected === name}
                    anySelected={props.selected !== null}
                    onClick={() => props.onChange(name)}
                    name={name}
                    title={title}
                    description={description}
                />
            ))}
        </div>
    );
};

const UsageButton = props =>{
    return (
        <Experiment id="790TNiS0Q6a04n00PIhu1g">
            <button
                onClick={props.onClick}
                title={props.description}
                style={{
                    color: "black",
                    backgroundColor: "white",
                    fontWeight: 400,
                    border: "2px solid " + (props.selected ? "var(--purple)" : "var(--light-grey)"),
                }}
            >
                <UsageIcon usage={props.name} color={fgColor(props.name)} />
                <p>{props.title}</p>
            </button>
            <button
                onClick={props.onClick}
                title={props.description}
                style={{
                    color: "white",
                    backgroundImage: bgImage(props.name),
                    fontWeight: 500,
                    border: "none",
                }}
                className={props.anySelected && !props.selected ? "grey-background" : null}
            >
                <UsageIcon usage={props.name} color={"white"} />
                <p>{props.title}</p>
            </button>
        </Experiment>
    );
};

const Experiment = props => {
    let variant = window.google_optimize.get(props.id);
    variant = parseInt(variant || "0");
    return props.children[variant];
};

export default Usage;
