import React from "react";
import routes from "../../routes.js"
import {Link} from "react-router-dom";

import "./Header.css";

/**
 * The page header.
 */
const Header = props => {
    return (
        <header>
            <div className="container">
                <Link className="header-icon-link" to={routes.home}>
                    <svg className="header-icon" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 1792 1792">
                        <path fill="currentColor" d="M 1160 41 A 895 895 0 1 0 1585 325 L 1423 459 A 685 685 0 1 1 842 213 L 701 477 884 574 Z" />
                    </svg>
                </Link>
                { /* <Searchbox /> */ }
                <Link className="nav-link" to={routes.home}>Hem</Link>
                <div className="separator" />
                <Link className="header-icon-link" to={location => location} onClick={props.toggleMenu}>
                    <svg className="header-icon" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 34 34">
                        <rect fill="currentColor" x="2" y="7" width="3" height="3" />
                        <rect fill="currentColor" x="8" y="7" width="24" height="3" />
                        <rect fill="currentColor" x="2" y="16" width="24" height="3" />
                        <rect fill="currentColor" x="29" y="16" width="3" height="3" />
                        <rect fill="currentColor" x="2" y="25" width="3" height="3" />
                        <rect fill="currentColor" x="8" y="25" width="24" height="3" />
                    </svg>
                </Link>
            </div>
        </header>
    );
};

export default Header;
