import React from "react";
import LayoutSelector from "./LayoutSelector";

import "./Header.css";

/**
 * The information header.
 */
const Header = props => {
    return (
        <div className="product-header">
            <div className="container">
                <div className="info">
                    <svg className="icon" viewBox="0 0 181 92">
                        <path fill="currentColor" d="M 0 85 H 9 V 2 Q 9 0 11 0 H 169 Q 171 0 171 2 V 85 H 180 V 88 Q 180 91 177 91 H 3 Q 0 91 0 88 z M 12 3 V 85 H 168 V 3 z" />
                    </svg>
                    <h1>Laptops</h1>
                    <p>Vlj dina preferenser nedan fr att sedan hitta en laptop som passar dig s bra som mjligt.</p>
                </div>
                <LayoutSelector onChange={props.onLayoutChange} layout={props.layout} />
            </div>
        </div>
    );
};

export default Header;
