import React from "react";
import routes from "../../routes";
import {Link} from "react-router-dom";

import "./Products.css";

/**
 * The product list for the category page.
 */
const Products = props => {
    let classes = "products";
    if (props.hidden) classes += " hidden";
    if (props.loading) classes += " loading";
    
    if (props.products === null) {
        if (window.google_optimize.get("Kh3bfh9oQWyAOB_XEw71yQ") === "1") {
            return (
                <div className={classes}>
                    <div className="message">
                        <p>Vlj <span style={{color: "var(--purple)"}}>anvndningsomrde</span>, <span style={{color: "var(--purple)"}}>budget</span> och <span style={{color: "var(--purple)"}}>prioriteringar</span>.<br/>Tryck sedan p verkstll fr att matcha produkter.</p>
                    </div>
                </div>
            );
        } else {
            return (
                <div className={classes}>
                    <div className="message">
                        <p>Vlj <span style={{color: "var(--purple)"}}>anvndningsomrde</span> och tryck sedan p verkstll fr att matcha produkter.</p>
                    </div>
                </div>
            );
        }
        
    } else if (props.products.length === 0) {
        return (
            <div className={classes}>
                <div className="message">
                    <p>Inga produkter matchade. Frsk att vara mindre specifik.</p>
                </div>
            </div>
        );
    } else {
        return (
            <div className={classes + " layout-" + props.layout}>
                <div className={"header"}>
                    <p>Namn</p>
                    <p>Pris</p>
                    <p>Skrm</p>
                </div>
                {props.products.map(({id, slug, name, active_price, images}, i) => (
                    <Product key={id + " " + slug + " " + (i === 0)}
                        url={routes.laptopProduct + "/" + id + "/" + slug}
                        src={images[0].url}
                        title={name}
                        price={active_price}
                        extra={null}
                        topChoice={i === 0}
                    />
                ))}
                <p>Hr tar det slut</p>
            </div>
        );
    }
};

function formatInt(value) {
    return value.toString().replace(/.(?=(...)+$)/g, "$& ");
}

/**
 * A single product.
 */
const Product = props => {
    return (
        <Link to={props.url} target="_blank" className={props.topChoice ? "top-choice" : ""}>
            <img className="product-image" src={props.src} alt="" />
            <p className="product-title">{props.title}</p>
            <p className="product-price">{formatInt(props.price)} kr</p>
            <p className="product-extra">{props.extra}</p>
        </Link>
    );
};

export default Products;
