import React from "react";

import "./LayoutSelector.css";

/**
 * The layout selector in the product list header. Used to change between the
 * grid and list layouts.
 */
const LayoutSelector = props =>{
    return (
        <div className="layout-selector">
            <svg
                viewBox="0 0 20 20"
                className={props.layout === "grid" ? "selected" : null}
                onClick={e => props.onChange("grid")}
            >
                <rect fill="currentColor" x="0" y="0" width="8" height="8" />
                <rect fill="currentColor" x="12" y="0" width="8" height="8" />
                <rect fill="currentColor" x="0" y="12" width="8" height="8" />
                <rect fill="currentColor" x="12" y="12" width="8" height="8" />
            </svg>
            <svg
                viewBox="0 0 20 20"
                className={props.layout === "list" ? "selected" : null}
                onClick={e => props.onChange("list")}
            >
                <rect fill="currentColor" x="0" y="0" width="20" height="8" />
                <rect fill="currentColor" x="0" y="12" width="20" height="8" />
            </svg>
        </div>
    );
};

export default LayoutSelector;
