import React from "react";
import {Link} from "react-router-dom";
import routes from "../../routes.js";

import "./Menu.css";

const Menu = props => {
    let classes = "menu";
    if (!props.visible) classes += " hidden";
    
    return (
        <div className={classes}>
            <div className="container">
                <svg className="close-icon" viewBox="0 0 20 20" onClick={props.toggleMenu}>
                    <path d="M 1 1 L 19 19 M 19 1 L 1 19" fill="none" stroke="currentColor" strokeLinecap="round" strokeWidth="2" />
                </svg>
                <h5>Information</h5>
                <Link to={routes.newsletters}>Nyheter</Link>
            </div>
        </div>
    )
};

export default Menu;
