import React, {Component} from "react";
import Cookies from "universal-cookie";
import axios from "axios";
import Customizers from "../../components/customizers/Customizers";
import Products from "../../components/products/Products";
import Header from "../../components/products/Header";

import "./Customization.css";

/**
 * The customization layout.
 */
class Customization extends Component {
    constructor(props) {
        super(props);
        
        // Load the default values.
        let selection = {usage: null};
        for (let {type, name, ...data} of this.props.customizers) {
            if (type === "usage") {
                selection[name] = null;
            } else if (type === "range") {
                selection[name] = {min: 0, max: 0};
            } else if (type === "priority") {
                let options = {none: false};
                for (let {name} of data.options)
                    options[name] = 0;
                selection[name] = options;
            } else if (type === "checkbox") {
                let options = {};
                for (let {name} of data.options)
                    options[name] = false;
                selection[name] = options;
            }
        }
        
        this.selection = selection;
        
        this.state = {
            selection,
            products: null,
            layout: "grid",
            customizersOpen: true,
            loading: false,
            popup: false,
            expanded: this.props.customizers.map(c => c.expanded),
            step: 0,
        };
        
        this.onValueChange = this.onValueChange.bind(this);
        this.onOptionChange = this.onOptionChange.bind(this);
        this.setOpen = this.setOpen.bind(this);
        this.onLayoutChange = this.onLayoutChange.bind(this);
        this.toggleCollapse = this.toggleCollapse.bind(this);

        this.selectionChanged = false;
        this.loadScheduled = false;
        
        this.popupShown = false;
        this.cookies = new Cookies();
    }
    /**
     * Loads the products to display.
     */
    async loadProducts() {
        this.setState({loading: true});

        try {
            // Get the products - using axios to allow easy addition of params
            let params = this.selection;
            let response = await axios.get("https://www.orpose.se/api/match/laptop", {params});
            let products = response.data.products;
            
            this.setState({products, loading: false});
        } catch (e) {
            console.error("Could not load the products: ", e);
            this.setState({products: null, loading: false});
        }
    }
    /**
     * Triggered when a customizer value is changed.
     */
    onValueChange(name, value) {
        // Load slider values if the usage changed.
        if (name === "usage" && value !== this.selection[name]) {
            for (let {type, name, ...data} of this.props.customizers) {
                if (type === "range") {
                    let {min, max} = data.ranges[value];
                    this.selection[name] = {min, max};
                }
            }
        }
        
        this.selection[name] = value;
        
        this.setState(state =>{
            let newState = {selection: this.selection};
            
            if (this.props.steps[state.step] === name)
                newState.step = state.step + 1;
            
            return newState;
        });
    }
    /**
     * Triggered when a customizer with multiple values is changed.
     */
    onOptionChange(name, option, value) {
        this.selection[name][option] = value;
        this.onValueChange(name, this.selection[name]);
    }
    /**
     * Triggered when the customizers is opened or closed.
     */
    setOpen(open) {
        this.setState({customizersOpen: open});
        this.openSurvey();
        this.loadProducts();
    }
    /**
     * Triggered when the layout changes.
     */
    onLayoutChange(layout) {
        this.setState({layout});
    }
    /**
     * Opens the survey if it has not yet been opened.
     */
    openSurvey() {
        if (this.cookies.get("categorySurveyShown") !== "true"){
            this.cookies.set("categorySurveyShown", "true", {
                path: "/",
                expires: new Date(Date.now() + 7 * 24 * 60 * 60 * 1000), // expire in one week
            });
        } else {
            this.popupShown = true;
        }
        
        if (!this.popupShown) {
            this.popupShown = true;
            this.setState({popup: true});
        }
    }
    /**
     * Collapses/expands a customizer.
     */
    toggleCollapse(i) {
        this.setState(state => {
            let expanded = state.expanded;
            expanded[i] = !expanded[i];
            return {expanded};
        })
    }
    
    render() {
        return (
            <div className="customization">
                <Header onLayoutChange={this.onLayoutChange} layout={this.state.layout} />
                <Customizers
                    customizers={this.props.customizers}
                    selection={this.state.selection}
                    open={this.state.customizersOpen}
                    expanded={this.state.expanded}
                    steps={this.props.steps}
                    step={this.state.step}
                    onValueChange={this.onValueChange}
                    onOptionChange={this.onOptionChange}
                    setOpen={this.setOpen}
                    toggleCollapse={this.toggleCollapse}
                />
                <Products
                    products={this.state.products}
                    layout={this.state.layout}
                    hidden={this.state.customizersOpen}
                    loading={this.state.loading}
                />
                <Advertisements />
                {this.state.popup && (
                    <Popup onClose={() => this.setState({popup: false})}/>
                )}
            </div>
        );
    }
}

/**
 * The advertisement space.
 */
const Advertisements = props => {
    return (
        <div className="advertisements" />
    );
};

/**
 * A suggestion popup.
 */
const Popup = props => {
    return (
        <div className="popup">
            <div className="content">
                <svg className="close-icon" viewBox="0 0 20 20" onClick={props.onClose}>
                    <path d="M 1 1 L 19 19 M 19 1 L 1 19" fill="none" stroke="currentColor" strokeLinecap="round" strokeWidth="2" />
                </svg>
                <h4>Hjlp oss att frbttra Orpose</h4>
                <p>Har du ngra frslag p skalternativ eller prioriteringar?</p>
                <Suggestions onClose={props.onClose} />
            </div>
        </div>
    );
};

/**
 * A text area used to submit suggestions (copied from Feedback.js).
 */
class Suggestions extends Component {
    constructor() {
        super();
        this.areaRef = React.createRef();
        this.state = {
            sent: false,
        };
        this.sentTimeout = null;
    }
    onChange() {
        // Limit the text to 128 characters.
        let textArea = this.areaRef.current;
        textArea.value = textArea.value.substring(0, 128);
    }
    submit() {
        // Limit the text to 128 characters.
        let textArea = this.areaRef.current;
        let text = textArea.value.substring(0, 128);
        
        if (text.length > 0) {
            // Submit the suggestions.
            fetch('https://www.orpose.se/api/survey/laptop', {
                method: 'POST',
                headers: {
                    "Content-Type": "application/x-www-form-urlencoded",
                },
                body: "answer=" + encodeURIComponent(text),
            }).then(response => {
                // Clear the textarea value.
                textArea.value = "";
            
                // Show the sent message.
                this.setState({sent: true});
                
                // Set a timeout to hide the message.
                if (this.sentTimeout === null) clearTimeout(this.sentTimeout);
                this.sentTimeout = setTimeout(this.props.onClose, 2000);
            });
        }
    }
    render() {
        return (
            <div className="feedback-area">
                <textarea ref={this.areaRef} placeholder="Skriv dina frslag hr" onChange={this.onChange.bind(this)}></textarea>
                <button onClick={this.submit.bind(this)}>Skicka</button>
                <p className={this.state.sent ? "visible" : null}>Frslag skickade!</p>
            </div>
        );
    }
}

export default Customization;
