import React, {Component} from "react";
import ReactSlider from "react-slider";

import "./Range.css";

/**
 * A range customizer. Allows the user to select a minimum and maximum value.
 */
class Range extends Component {
    constructor(props) {
        super(props);
        
        // The text in the minimum and maximum inputs.
        this.state = {
            minText: this.props.selectedMin.toString(),
            maxText: this.props.selectedMax.toString(),
        };
        
        this.onSlider = this.onSlider.bind(this);
        this.onInput = this.onInput.bind(this);
        this.onBlur = this.onBlur.bind(this);
    }
    
    /**
     * Change the text when the selection changes.
     */
    componentDidUpdate(prevProps) {
        if (prevProps.selectedMin !== this.props.selectedMin)
            this.setState({minText: this.props.selectedMin.toString()});
        if (prevProps.selectedMax !== this.props.selectedMax)
            this.setState({maxText: this.props.selectedMax.toString()});
    }
    
    /**
     * Triggered when the slider value is changed.
     */
    onSlider(values) {
        // Trigger the handler.
        let [min, max] = values;
        this.props.onChange(min, max);
    }
    /**
     * Triggered when the text in a text box is changed.
     */
    onInput(event) {
        let target = event.target;
        
        // Remove non-digits from the text.
        let value = target.value.replace(/\D/, "");
        target.value = value;
        
        // Update the state with the new text.
        if (target.name === "min") {
            this.setState({minText: value});
        } else if (target.name === "max") {
            this.setState({maxText: value});
        }
    }
    /**
     * Triggered when a text box loses focus.
     */
    onBlur(event) {
        if (event.type === "blur" || event.key === "Enter") {
            if (event.type === "keypress") event.preventDefault();
            
            // Parse the value in the text box
            let value = parseInt(event.target.value || "0");
            
            let minDist = this.minDistance();
            
            if (event.target.name === "min") {
                // Make sure the value is within the bounds.
                value = Math.max(value, this.props.range.min);
                value = Math.min(value, this.props.range.max - minDist);
                
                // Move the other handle if necessary.
                let max = Math.max(value + minDist, this.props.selectedMax);
                
                this.onSlider([value, max]);
            } else if (event.target.name === "max") {
                // Make sure the value is within the bounds.
                value = Math.min(value, this.props.range.max);
                value = Math.max(value, this.props.range.min + minDist);
                
                // Move the other handle if necessary.
                let min = Math.min(value - minDist, this.props.selectedMin);
                
                this.onSlider([min, value]);
            }
        }
    }
    /**
     * The minimum distance between the handlers.
     */
    minDistance() {
        let {min, max, step} = this.props.range;
        let steps = Math.floor((max - min) / step);
        return Math.max(1, Math.round(steps * 0.05)) * step;
    }
    render() {
        let classes = "range-container";
        if (this.props.disabled) classes += " disabled";
        
        if (this.props.range) {
            return (
                <div className={classes}>
                    <input type="text" name="min" value={this.state.minText} onChange={this.onInput} onKeyPress={this.onBlur} onBlur={this.onBlur} disabled={this.props.disabled} />
                    <div className="separator" />
                    <input type="text" name="max" value={this.state.maxText} onChange={this.onInput} onKeyPress={this.onBlur} onBlur={this.onBlur} disabled={this.props.disabled} />
                    <div className="slider-container">
                        <ReactSlider
                            className="horizontal-slider multi-slider"
                            thumbClassName="thumb"
                            trackClassName="track"
                            value={[this.props.selectedMin, this.props.selectedMax]}
                            min={this.props.range.min}
                            max={this.props.range.max}
                            ariaLabel={['Min', 'Max']}
                            ariaValuetext={state => `Thumb value ${state.valueNow}`}
                            renderThumb={(props, state) => <div {...props}></div>}
                            onChange={(values) => this.onSlider(values)}
                            onAfterChange={(values) => this.onSlider(values)}
                            pearling
                            step={this.props.range.step}
                            minDistance={this.minDistance()}
                            disabled={this.props.disabled}
                        />
                    </div>
                </div>
            );
        } else {
            return (
                <div className={classes}>
                    <input type="text" value="N/A" disabled={true} />
                    <div className="separator" />
                    <input type="text" value="N/A" disabled={true} />
                    <div className="slider-container">
                        <ReactSlider
                            className="horizontal-slider multi-slider"
                            thumbClassName="thumb"
                            trackClassName="track"
                            value={[0, 1]}
                            min={0}
                            max={1}
                            ariaLabel={['Min', 'Max']}
                            ariaValuetext={state => `Thumb value ${state.valueNow}`}
                            renderThumb={(props, state) => <div {...props}></div>}
                            onChange={(values) => this.onSlider(values, false)}
                            onAfterChange={(values) => this.onSlider(values, true)}
                            pearling
                            step={1}
                            minDistance={1}
                            disabled={true}
                        />
                    </div>
                </div>
            );
        }
    }
}

export default Range;
