const fgColor = usage => {
    if (usage === "gaming") {
        return "red";
    } else if (usage === "general") {
        return "blue";
    } else{
        return null;
    }
};
const bgImage = usage => {
    if (usage === "gaming") {
        return "linear-gradient(to right, #872031, #BE3445)";
    } else if (usage === "general") {
        return "linear-gradient(to right, #104374, #155d99)"
    } else{
        return null;
    }
};

export default UsageIcon;
export {fgColor, bgImage};
