import React from "react";
import Helmet from "react-helmet";
import {Link} from "react-router-dom";
import routes from "../../routes";
import Layout from "../../components/Layout";
import Document from "../../components/Document";

const Deals = () => {
    return (
        <Layout>
            <Helmet>
                <title>Deals | Orpose.se</title>
                <meta name="description" content={"Hitta rtt produkt fr just dina krav och preferenser p Orpose"} />
                <link rel="canonical" href={"https://www.orpose.se/deals"} />
            </Helmet>
            <Document>
                <h3>Under konstruktion</h3>
                <Link to={routes.home}>Tillbaka till startsidan</Link>
            </Document>
        </Layout>
    );
};

export default Deals;
