import React, {Component} from "react";
import {Route, Switch, BrowserRouter, Redirect, withRouter} from "react-router-dom";
import { Provider, connect } from "react-redux";
import { createStore, applyMiddleware } from "redux";
import thunk from "redux-thunk";

import {auth} from "./actions";
import OrposeApp from "./reducers"
import routes from "./routes.js";
import "./App.css";

import LaptopCategory from "./routes/categories/Laptop";

import LaptopProduct from "./routes/product/Laptop";

import Orpose from "./routes/Orpose";

import Login from "./routes/auth/Login";
import Logout from "./routes/auth/Logout"
import Register from "./routes/auth/Register"

import NotFound from "./routes/error/NotFound";

import About from "./routes/extra/About";
import Contact from "./routes/extra/Contact";
import Career from "./routes/extra/Career";
import Disclaimer from "./routes/extra/Disclaimer";
import Cookies from "./routes/extra/Cookies";
import Sitemap from "./routes/extra/Sitemap";
import Deals from "./routes/extra/Deals";

import Newsletter from "./routes/newsletter/Newsletter";
import Newsletters from "./routes/newsletter/Newsletters";

let store = createStore(OrposeApp, applyMiddleware(thunk));

class ScrollToTop extends Component {
    componentDidUpdate(prevProps) {
        if (this.props.location !== prevProps.location) {
            window.scrollTo(0, 0);
        }
    }
  
    render() {
        return this.props.children;
    }
}
ScrollToTop = withRouter(ScrollToTop);

class RootContainerComponent extends Component {
    constructor() {
        super();
        
        this.checkAnalytics = this.checkAnalytics.bind(this);
        this.state = {analyticsLoaded: false};
    }
    
    componentDidMount() {
        this.props.loadUser();
        this.checkAnalytics();
    }
    
    checkAnalytics() {
        if (window.google_optimize) {
            this.setState({analyticsLoaded: true});
        } else {
            setTimeout(this.checkAnalytics, 100);
        }
    }

    PrivateRoute = ({component: ChildComponent, ...rest}) => {
        return <Route {...rest} render={props => {
            if (this.props.auth.isLoading) {
                return <em>Loading...</em>;
            } else if (!this.props.auth.isAuthenticated) {
                return <Redirect to="/login" />;
            } else {
                return <ChildComponent {...props} auth={this.props.auth} />
            }
        }} />
    };

    render() {
        if (!this.state.analyticsLoaded) {
            return <div />;
        }
        
        let {PrivateRoute} = this;
        return (
            <div>
                <BrowserRouter>
                    <ScrollToTop>
                        <Switch>
                            {/* Main routes */}
                            <Route exact path={routes.home} component={Orpose} />
                            <Route path={routes.laptopProduct+"/:id/:slug"} component={LaptopProduct} />

                            {/* Category routes */}
                            <Route path={routes.laptopCategory} component={LaptopCategory} />

                            {/* Auth routes */}
                            <Route exact path={routes.login} component={Login} />
                            <Route exact path={routes.register} component={Register} />
                            <PrivateRoute exact path={routes.logout} component={Logout} />

                            {/* Extra routes */}
                            <Route exact path={routes.cookies} component={Cookies} />
                            <Route exact path={routes.sitemap} component={Sitemap} />
                            <Route exact path={routes.disclaimer} component={Disclaimer} />
                            <Route exact path={routes.about} component={About} />
                            <Route exact path={routes.contact} component={Contact} />
                            <Route exact path={routes.career} component={Career} />
                            <Route exact path={routes.deals} component={Deals} />
                            
                            {/* Newsletters */}
                            <Route exact path={routes.newsletters} component={Newsletters} />
                            <Route path={routes.newsletters+"/:id"} component={Newsletter} />

                            {/* Error routes */}
                            <Route component={NotFound} />
                        </Switch>
                    </ ScrollToTop>
                </BrowserRouter>
            </div>
        );
    }
}

const mapStateToProps = state => {
    return {
        auth: state.auth,
    };
};

const mapDispatchToProps = dispatch => {
    return {
        loadUser: () => {
            return dispatch(auth.loadUser());
        },
    };
};

let RootContainer = connect(mapStateToProps, mapDispatchToProps)(RootContainerComponent);

class App extends Component {
    render() {
        return (
            <Provider store={store}>
                <RootContainer />
            </Provider>
        );
    }
}

export default App;
